<template>
	<div class="help-details">
		<div class="content" :style="scrollHeight">
			<section class="details-t">   	   	
    	   	  <h3>{{details.title}}</h3>
    	   	  <p>{{details.created}}</p>
	    </section>
	    <section class="details-c" v-html="details.content"></section>
		</div>
	</div>
</template>

<script>
export default {
  name: 'HelpCenterDetails',
  data(){
  	return{
  		details:{}
  	}
  },
  mounted () {
    this.getHelpDetails();
  },
  methods: {
    getHelpDetails(){
      let _this = this;
      _this.$http.get(_this.$lib.url + 'helping/detail?id='+_this.$route.params.id).then(res => {
			console.log(res);
			if (res.status == 0) {
				_this.details = res.data;
			}
		}).catch(err => {})
    },
  },
  computed:{
  	scrollHeight(){
  		return "height:"+((window.screen.height)-56)+"px"
  	}
  },
  watch:{
  	'$route'(to,from){
  		if(to.path=="/helpCenterDetails/"+this.$route.params.id && from.path=="/helpCenter"){
  			this.getHelpDetails();
  		}
  	}
  }
 }
</script>

<style scoped>
.help-details{
	overflow-y: scroll;
    background-color: #FAF8F5;
}
  .help-details .details-t{
    text-align: center;
    padding-left: 10px;
    padding-right: 10px;
   }

	.help-details .details-bg img{height: 185px;width:100%;}
	 .details-t h3{margin: 20px 0 0 0;} 
  .help-details .details-c{padding: 0 10px;padding-bottom: 30px;padding-top: 10px;}
  .help-details .details-c img{
    width: 100% !important;
    height: auto !important;
  }
</style>


